/* stylelint-disable no-descending-specificity */
.van-image {
  position: relative;
  display: inline-block;
}
.van-image--round {
  overflow: hidden;
  border-radius: 50%;
}
.van-image--round .van-image__img {
  border-radius: inherit;
}
.van-image__img,
.van-image__error,
.van-image__loading {
  display: block;
  width: 100%;
  height: 100%;
}
.van-image__error,
.van-image__loading {
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #969799;
  color: var(--image-placeholder-text-color, #969799);
  font-size: 0.59733rem;
  font-size: var(--image-placeholder-font-size, 0.59733rem);
  background-color: #f7f8fa;
  background-color: var(--image-placeholder-background-color, #f7f8fa);
}
.van-image__loading-icon {
  color: #dcdee0;
  color: var(--image-loading-icon-color, #dcdee0);
  font-size: 1.36533rem !important;
  font-size: var(--image-loading-icon-size, 1.36533rem) !important;
}
.van-image__error-icon {
  color: #dcdee0;
  color: var(--image-error-icon-color, #dcdee0);
  font-size: 1.36533rem !important;
  font-size: var(--image-error-icon-size, 1.36533rem) !important;
}
.image-class .taro-img__mode-aspectfit {
  width: 100%;
  height: 100%;
  max-width: none !important;
  max-height: none !important;
  object-fit: contain;
}
.image-class .taro-img__mode-center {
  object-fit: none;
  display: block;
  width: 100%;
  height: 100%;
}
.image-class .taro-img__mode-heightfix {
  height: 100%;
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
